const selectElements = document.querySelectorAll(".projectId-input");
const container = document.querySelector("#checklist-input-container");
const checklistItemTemplate = document.querySelector("#checklist-item");
export function updateProjectOptions(projArr) {
    selectElements.forEach((select) => {
        select.innerHTML = "";
        projArr.forEach((project) => {
            const option = document.createElement("option");
            option.innerText = project.title;
            option.value = project.projectId.toString();
            select.insertAdjacentElement("beforeend", option);
        });
    });
}
export function renderChecklistField(e) {
    if (!checklistItemTemplate || !container)
        return;
    e.preventDefault();
    const instance = checklistItemTemplate.content.cloneNode(true);
    container.appendChild(instance);
}
