import { showToDoList } from "./todoListController";
const switchFormContainer = document.querySelector(".switch-form-container");
const checklistForm = document.querySelector("#checklist-form");
const inputForm = document.querySelector("#todo-form");
const switchButtons = document.querySelectorAll(".switch-btn");
export function initSwitchTypeController(state) {
    if (!switchFormContainer || !inputForm || !checklistForm)
        return;
    switchFormContainer.addEventListener("click", (e) => handleSwitchForm(e, inputForm, checklistForm));
    function handleSwitchForm(e, inputForm, checklistForm) {
        const targetBtn = e.target.closest("button");
        if (!targetBtn || targetBtn.classList.contains("selected"))
            return;
        switchButtons.forEach((btn) => btn.classList.remove("selected"));
        targetBtn.classList.add("selected");
        // Update selected type
        state.selectedType = targetBtn.textContent?.toLowerCase() ?? "item";
        // toggles between the two forms
        inputForm.classList.toggle("hidden");
        checklistForm.classList.toggle("hidden");
        showToDoList();
    }
}
